.section_thir{
    width:100%;
}
.section_thir figure h2{
    width:50%; height: 200px;
    padding-left: 5%;
    font-size: 7vw;
    position: sticky;
    top:0; left:0;
    /* background: linear-gradient(#fff 80%, transparent ); */
    z-index: 1;
}
.section_thir figure .profile{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    position: relative;
}
.skill{
    width:60%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
}
.skill_good,.skill_mid{
    width:100%;    height: 150vh;
    margin-top:20px;
    padding-top:40vh;
    padding-left:10%;
    font-size: 1em;
    line-height: 2em;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    /* border:1px solid #000; */
}
.skill div h3{
    overflow: hidden;
    margin-bottom: 20px;
}
.skill div h3 span{
    display: inline-block;
    font-size: 2em;
    /* transform: translateY(20px); */
    line-height: 1.2em;
    /* opacity: 0; */
}

.skill div p{
    width:90%;
    margin: 10px auto;
    overflow: hidden;
}
.skill div p span{
    display: block;
    transition: 0.5s;
    transform: translateY(20px);
    opacity: 0;
}
.skill div p span img{
    height: 40px;
    /* margin-right:30px; */
}
.skill div p span:nth-child(1){
    font-size: 2em;
}
.skill_good{
    margin-top: 50vh;
}
.my-profile{
    position:sticky;
    top: 150px; right:0;
    width:50%; height: 90vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* perspective: 1000px; */
    z-index: 1;
}
.my-profile::before{
    content:'';
    position:absolute;
    top:0; left: 0;
    width:100%; height: 100%;
    background: linear-gradient(#f00, #f0f);
    clip-path: circle(30% at right 70%);
  }
/* .my-profile::after{
    content:'';
    position:absolute;
    top:0; left: 0;
    width:100%; height: 100%;
    background: linear-gradient(#2196f3, #e91e63);
    clip-path: circle(20% at 10% 10%);
    z-index: -1;
  } */
.my-profile .card{
    position:relative;
    width: 35vw; height: 90vh;
    /* margin:30px; */
    border-radius: 15px;
    background: rgba(255,255,255, 0.1);
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    border-top:1px solid rgba(255,255,255,0,5);
    border-left:1px solid rgba(255,255,255,0,5);
    backdrop-filter: blur(5px);
    box-shadow: 20px 20px 50px rgba(0,0,0,0.5);
    transition: all 0.5s ease;
    transform : translateY(-100px)rotateX(0) rotateY(0);
}
.my-profile .card img{
    width:60%;
    transform: translateY(-10px);
}
/* .my-profile .card img:hover{
    filter:grayscale(1)
} */
.card p:nth-child(2){
    font-weight: bold;
    font-size: 1.6em;
}
.card p:nth-child(3){
    width: 70%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.disc2 b{
    display: inline-block;
}
.my-profile div{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.my-profile div p{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 1.2em;
    margin: 20px;
}
.my-profile div p b{
    font-size: 0.8em;
    line-height: 1.7;
}
.phrase{
    width:100%; height: 200vh;
    position:sticky;
    left: 0; top:50%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    opacity: 0;
}
.phrase div{
    padding: 30px 0;
    text-align: center;
    -webkit-box-reflect: below -1.5em linear-gradient(transparent 30%,#222);
}
.phrase div span{
    transition:0.5s;
    font-weight: bold;
    font-size: 2em;
    opacity: 0;
    /* color:transparent; */
    /* font-size: 2em; */
    /* background-image: linear-gradient(90deg, red, blue); */
    /* background-clip: text; */
}
.phrase div span.active{
    display: inline-block;
    transition:0.5s;
    /* filter: blur(5px); */
    animation: aniSet 0.7s forwards;
    opacity: 0;
}
@keyframes aniSet{
    0%{transform:translateY(-100px); filter: blur(5px);}
    100%{transform:translateY(0px); filter: blur(0); opacity: 1;}
}
@media (min-width: 768px) and (max-width: 1119px) {
    .section_thir figure h2{
        font-size: 10vw;
    }
    .my-profile{
        position:sticky;
        top: 250px; right:0;
        width:50%; height: 80vh;
    }
    .my-profile .card{
        width: 100%; height: 80vh;
    }
    .skill{
        width:50%;
        justify-content: flex-start;
    }
    .skill_good,
    .skill_mid{
        margin-top: 0;
        padding-top: 30vh;
        padding-left:5%;
}
    .skill div h3 span{
        font-size: 1.5em;
    }
    .skill div p span{
        font-size: 0.9em;
    }
}
@media (max-width:767px){
    .section_thir figure h2{
        width:100%; height: 200px;
        font-size: 3em;
    }
    .skill{
        width:100%;
    }
    .skill_good,.skill_mid{
    padding-top: 100px;
    font-size: 1em;
    line-height: 1.5em;
    padding-left:0;
    }
    .skill div h3 span{
        font-size: 1.3em;
        margin-bottom: 10px;
    }
    .skill div p{
        width:95%;
        margin: 10px auto;
        overflow: hidden;
    }
    .skill_good{
        margin-top: 0;
    }
    .my-profile{
    position:relative;
    top: 0; left:0;
    width:100%;
    }
    .my-profile .card{
    width: 95%;
    margin:0;
    height: auto;
    }
    .section_thir figure .profile{
        flex-direction: column;
        align-items: center;
    }
}