}
.main_center{
    width: 800px;
    margin: 200px auto 0;
    position: relative;
    bottom: 12%;
    transform: translateX(0);
    transition: 1s ease-in-out;
}
.txt-all{
    width:auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    transition: 1.5s ease-in-out;
    z-index: 1;
}
.txt1{
    transform: translate(90px,10px) rotate(5deg);
}
.txt2{
    transform: translate(-130px,10px) rotate(-3deg);
}
.txt3{
    transform: translate(50px,10px) rotate(0deg);
}
.txt4{
    transform: translate(150px,5px) rotate(0deg);
}
.txt5{
    transform: translate(0,0) rotate(0deg);
}
.txt-all span{
    display: inline-block;
    font-size: 8em;
    word-break: keep-all;
    line-height: 1;
    animation: textUp 1s forwards;
    transform:translateY(100%);
    letter-spacing: -0.1em;
    font-family: 'Noto Sans KR', sans-serif;
    font-weight: 300;
}
.txt4 b{
    font-weight: 400;
    /* color: #e91e63; */
}
.txt5 b{
    font-weight: 400;
    /* color: #2196f3; */
}
.main_bottom_left{
    position:absolute;
    left: 40px; bottom:30px;
}
.main_bottom_left div{
    margin-top: 25px;
    overflow: hidden;
}
.main_bottom_left div ul{
    font-size: 16px;
    transform:translateY(-100%);
    animation: textDown 1s forwards;
}
.main_bottom_left ul li{
    font-size: 16px;
    line-height: 1.4em;
}
.main_bottom_left div:nth-child(1) ul{
    animation-delay: 0.8s;
}
.main_bottom_left div:nth-child(2) ul{
    animation-delay: 1.0s;
}
.main_bottom_left div:nth-child(3) ul{
    animation-delay: 1.3s;
}
.main_bottom_right{
    position:absolute;
    right: 40px; bottom:30px;
    text-align: end;
}
.main_bottom_right ul li{
    font-weight: bold;
    line-height: 1.5em;
    margin-bottom: 10px;
    overflow:hidden;
}
.main_bottom_right ul li span{
    display: inline-block;
    font-size: 1.5em;
    transform:translateY(-100%);
    animation: textDown 1s ease forwards;
}
.main_bottom_right ul li:nth-child(1) span {
    animation-delay: 1.5s;
}
.main_bottom_right ul li:nth-child(2) span{
    animation-delay: 1.8s;
}
.main_bottom_right ul li:nth-child(3) span{
    animation-delay: 2.1s;
}
.main_bottom_right p{
    overflow: hidden;
}
.main_bottom_right p span{
    display: inline-block;
    font-size: 16px;
    line-height: 1.5em;
    animation: scrolldown 3s infinite;
}
@keyframes scrolldown{
    0%{transform: translateY(-100%);}
    30%{transform: translateY(0);}
    70%{transform: translateY(0);}
    90%,100%{transform: translateY(100%);}
}
@keyframes textUp{
    0%{transform:translateY(100%)}
    100%{transform:translateY(0)}
}
@keyframes textDown{
    0%{transform:translateY(-100%)}
    100%{transform:translateY(0)}
}
@media (min-width: 768px) and (max-width: 1119px) {
    .main_center{
        width: 600px;
        margin: 200px auto;
        left: calc(50%-300px); bottom: 0    ;
    }
    .txt-all span{
        font-size: 6em;
    }
  }
  
@media (max-width: 767px) {
    .section_fir{
        height: 900px; width: 100%;
    }
    .main_center{
        width: 300px;
        margin: 100px auto;
        left: calc(50%-150px); bottom: 0;
    }
    .txt-all span{
        font-size: 2.5em;
    }
    .main_top div{
        padding: 10px 0 0 15px;
    }
    .txt1{
        transform: translate(45px,10px) rotate(5deg);
    }
    .txt2{
        transform: translate(-65px,10px) rotate(-3deg);
    }
    .txt3{
        transform: translate(0px,10px) rotate(0deg);
    }
    .txt4{
        transform: translate(75px,5px) rotate(0deg);
    }
    .txt5{
        transform: translate(0,0) rotate(0deg);
    }
    .main_bottom_left{
        position:relative;
        left: 10px; bottom:30px;
    }
    .main_bottom_left div ul{
        font-size: 14px;
    }
    .main_bottom_left ul li{
        font-size: 14px;
    }
}