.section_four{
    width:100%; height: 100vh;
    margin: 0 auto;
    /* background: #343434; */
    overflow: hidden;
}
.section_four h2{
    height: 200px;
    padding-left: 5%;
    font-size: 7vw;
}
.section_four >div{
    display: flex;
    justify-content: space-around;
    align-items: flex-end;
}
.arrow svg{
    width: 700px; height:200px;
}
.contactMe{
    display: flex;
    justify-content:space-between;
    align-items: center;
}
.contactMe p{
    width:auto;
    white-space: pre-line;
    cursor: pointer;
    text-transform: uppercase;
}
.contactMe p:hover{
    transition:0.5s;
    text-shadow: 1px 1px 2px rgba(0,0,0,0.5);
}
.circle{
    position:relative;
    width:700px; height:700px;
    background-color: #fff;
    transform: translateX(0) rotateY(0deg);
}
.circle span{
    position:absolute;
    left:0; top:0;
    right:0; bottom:0;
    border: 0.7px solid #000;
    border-radius: 42% 62% 65% 40% /
                   41% 44% 56% 59%;
}
@keyframes circleShow{
    0%{ transform:rotate(0deg)}
    100%{ transform:rotate(360deg)}
}
@keyframes circleShow2{
    0%{ transform:rotate(360deg)}
    100%{ transform:rotate(0deg)}
}
.circle >span:nth-child(3){
    animation: circleShow 5s linear infinite;
    animation-play-state: running;
}
.circle >span:nth-child(4){
    animation: circleShow2 3s linear infinite;    
    animation-play-state: running;
}
.circle >span:nth-child(5){
    animation: circleShow 4s linear infinite;
    animation-play-state: running;
}
.circle:hover span {
    /* background: linear-gradient(rgba(0, 4, 252, 0.2), rgba(252, 4, 198, 0.2)50%); */
    background: rgba(0,0,0,0.1);
    animation-play-state: paused;
}
.resume{
    position:absolute;
    left: 50%; top:50%;
    width:400px; height:400px;
    transition:0.7s;
    transform: translate(50%,-50%) rotateY(-90deg);
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    transform-origin: left;
}
.resume.active{
    transition:0.7s;    
    transform: translate(-50%,-50%) rotateY(0deg);
}
.resume a{
    text-decoration: none;
    font-size: 2em;
    color:#000;
    cursor: pointer;
}

.contact-pop{
    position:absolute;
    left: 50%; top:50%;
    width:400px; height:400px;
    /* background: #fff; */
    /* border-radius: 15px; */
    /* box-shadow: 5px 5px 20px rgba(0,0,0,0.1); */
    /* backdrop-filter: blur(5px); */
    transform-origin: right;
    transition:0.7s;
    transform: translate(-150%,-50%) rotateY(90deg);
    z-index: 1;
}
.contact-pop.active{
    display: block;
    transition:0.7s;
    transform: translate(-50%,-50%) rotateY(0deg);
}
.contact-form{
    width: 100%; height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.form-elements{
    position:relative;
    box-sizing: border-box;
    width:100%;
}
.inputBox:not(:nth-child(3)){
    margin-bottom: 20px;
}
.inputBox input{
    position:absolute;
    background: transparent;
    border:none;
    border-bottom:2px solid rgba(0,0,0,0.5);
    outline:none;
    font-size: 1em;
    padding: 7px 0;
    width: 100%;
    transition:0.5s;
}
.inputBox label{
    position:relative;
    font-size: 1em;
    padding:10px 0;
    display: inline-block;
    pointer-events: none;
    transition:0.5s;
}
.inputBox.active input{
    border-bottom: 2px solid rgba(0,0,0,1);
}
.inputBox.active label{
    color:rgba(0,0,0,0.3);
    transform:translateY(-30px);
}
.contact-form textarea{
    display: block;
    background: transparent;
    color: #000;
    resize: none;
    border: 2px solid #000;
    width:100%;
    font-size: 1em;
    font-family:sans-serif;
}
.form-elements button{
    position:absolute;  
    left: 50%; bottom:-60px;
    transform: translate(-50%,0);
    padding: 5px 10px;
    outline: none;
    border: none;
    /* border: 1px solid rgba(0,0,0,0.5); */
    border-radius: 5px;
    background: transparent;
    font-size: 1em;
    transition:0.5s;
    cursor: pointer;
}
.form-elements button:hover{
    text-shadow: 1px 1px 2px rgba(0,0,0,0.5);
}
.thankyou-message{
    display: none;
}
@media (min-width: 768px) and (max-width: 1119px) {
    .section_four h2{
        font-size: 10vw;
    }
    .section_four >div{
        display: flex;
        flex-direction: column;
        justify-content:center;
        align-items: center;
    }
    .circle{
        position:relative;
        width:600px; height:600px;
    }
    .arrow svg{
        transform:translate(-35%,20%)rotate(35deg)
    }
    .contactMe{
        justify-content:flex-end;
        transform: translate(10%,-300%);
        margin-right: 20px;
    }
    .contactMe p{
        margin-left:70px;
    }
}
@media (max-width: 767px){
    .section_four{
        width:100%;
    }
    .section_four h2{
        font-size: 3em;
    }
    .section_four figure{
        width:100%;
    }
    .section_four >div{
        display: flex;
        flex-direction: column;
        justify-content:center;
        align-items: center;
    }
    .circle{
        position:relative;
        width:300px; height:300px;
    }
    .circle span{
        display: none;
    }
    .arrow svg{
        width: 500px;
        transform:translate(0,-35%)rotate(155deg)
    }
    .contactMe{
        width:100%;
        flex-direction: column;
        justify-content:flex-start;
        align-items: flex-start;
        transform: translate(0,-70%);
    }
    .contactMe p{
        margin-bottom: 20px;
        margin-left: 30px;
    }
    .contact-pop,.resume{
        width:100%;
        padding:20px 10px;
        box-shadow: 2px 2px 20px rgba(0,0,0,0.3);
    }
}