.section_sec{
    width:100%;
    min-height: 100vh;
    background: #fff;
    margin:0 auto;
}
.sec_all{
    margin:0 auto;
    box-shadow: 1px 1px 10px rgba(0,0,0,1);
    border-radius: 50%;
    overflow: hidden;
    transform: translateY(-55%) scale(0.3) rotate(0);
    transform-origin: center center;
    transition:.5s;
    width: 1000px; height:1000px;
    animation: centerCircle 3s infinite;
    filter: grayscale(1) blur(3px);
}
.sec_all h2{
    font-size: 10vw;
    text-align: center;
    padding:50px 0 50px 0;
}
.project-list{
    width:90%;
    margin:50px auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}
.project-list div{
    width:30%;
    margin-bottom:50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}
.project-list div img{
    width:100%;
    pointer-events: none;
}
.project-list div b{
    font-size: 2.3vw;
    padding: 10px 0 0 15px;
    pointer-events: none;
}
.project-list div:hover{
    transition:0.5s;
    box-shadow: 2px 0 10px rgba(0,0,0,0.4);
}
.project-list div i{
    font-style: normal;
    font-size: 1vw;
    padding:20px 10px;
    pointer-events: none;
}
.project-list div:nth-child(3n+1){
    transform: translateY(30px);
}
.project-list div:nth-child(3n){
    transform: translateY(40px);
}
.scroll-section{
    position:relative;
    width:100%;
    overflow: hidden;
    height:800px;
}
.scroll-section .scroll{
    width: 100%;
    display: flex;
}
.scroll-section .scroll > div{
    color:#000;
    font-size: 4em;
    background: #fff;
    white-space: nowrap;
    font-weight: 900;
    text-transform: uppercase;
    animation: animate 40s linear infinite;
    animation-delay: -40s;
}
.scroll-section .scroll > div:nth-child(2){
    animation: animate2 40s linear infinite;
    animation-delay: -20s;

}
.scroll-section .scroll span{
    -webkit-text-stroke: 2px #000;
    color:transparent;
}
@keyframes animate{
    0%{
        transform: translateX(100%);
    }
    100%{
        transform: translateX(-100%);
    }
}
@keyframes animate2{
    0%{
        transform: translateX(0%);
    }
    100%{
        transform: translateX(-200%);
    }
}
.scroll-section .scroll.text1{
    transform: rotate(20deg) translateX(100px) translateY(320px);
    box-shadow: 0 5px 15px rgba(0,0,0,0.4);
}
.scroll-section .scroll.text2{
    transform: rotate(-6deg) translateX(0) translateY(140px);
    box-shadow: 0 5px 15px rgba(0,0,0,0.4);
}
.scroll-section .scroll.text3{
    transform: rotate(3deg) translateX(0) translateY(270px);
    box-shadow: 0 5px 15px rgba(0,0,0,0.4);
}
.slider{
    position: fixed;
    left: 0; top:0;
    width:100%; height: 100vh;
    transition:0.7s;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center; 
    z-index: 10000;
    display: none;
}
.slider.on{
    display: block;
}
.slider.on button{
    border:none;
    background-color: transparent;
    position:absolute;
    right:30px; top:20px;
    z-index: 10002;
} 
.right-arrow{
    position:absolute;
    top:50%; right:32px;
    font-size: 3em;
    color: #000;
    z-index: 10001;
    cursor: pointer;
    user-select: none;
}
.left-arrow{
    position:absolute;
    top:50%; left:32px;
    font-size: 3em;
    color: #000;
    z-index: 10001;
    cursor: pointer;
    user-select: none;
}
.slide{
    opacity: 0;
    transition-duration: 1s ease;
    transform:scale(0.5);
}
.slide.active{
    opacity: 1;
    transition-duration: 1s;
    transform:scale(1);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width:60%;
    margin: 50px auto;
}
.project-header::after{
    content:'';
    display: block;
    width:100%; height: 50px;
    /* border-bottom: 1px solid #000; */
}
.project-header h2{
    font-size: 3em;
    text-align: center;
}
.project-header a{
    text-decoration: none;
    font-size: 1.5em;
}
.project-header div{
    margin:10px auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.project-detail ul{
    font-size: 1.5em;
    margin:25px auto;
}
.project-detail ul li{
    font-size: 0.7em;
    padding: 10px 0 0 15px;
}
.image{
    width: 80%;
    border-radius: 10px;
    border: 2px solid #000;
    border-radius:10px;
}
.sub-img{
    width: 100%;
    border: 2px dotted #000;
}
@media (min-width: 768px) and (max-width: 1119px) {
    .sec_all{
        width: 800px; height:800px;
    }
    .sec_all h2{
        font-size: 13vw;
        padding: 30px 0 0 0;
    }
    .project-list div{
        width: 45%;
    }
    .project-list div b{
        font-size: 2em;
        padding: 10px 0 0 15px;
    }
    .project-list div i{
        font-style: normal;
        font-size: 0.9em;
        padding: 10px;
    }
    .project-list div:nth-child(3n+1){
        transform: translateY(0);
    }
    .project-list div:nth-child(3n){
        transform: translateY(0);
    }
    .scroll-section{
        height:600px;
        top:-150px;
    }
}
@media (max-width: 767px) {
    .sec_all{
        width:300px; height: 300px;
        transform: translateY(-40%) scale(0.3);
    }
    .sec_all h2{
        font-size: 3em;
    }
    .project-list{
        width:100%;
    }
    .project-list div{
        width:90%;
        margin:0 auto;
        margin-bottom:50px;
    }
    .project-list div b{
        font-size: 2em;
    }
    .project-list div i{
        font-size: 0.9em;
        padding: 10px 5px;
    }
    .scroll-section{
       height:450px;
       transform: translateY(-100px);
       overflow: hidden;
    }
    .scroll-section .scroll > div{
    font-size: 2em;
    }
    .project-list div:nth-child(3n+1){
        transform: translateY(0);
    }
    .project-list div:nth-child(3n){
        transform: translateY(0);
    }
}