@-webkit-keyframes circleShow{
    0%{ -webkit-transform:rotate(0deg); transform:rotate(0deg)}
    100%{ -webkit-transform:rotate(360deg); transform:rotate(360deg)}
}
@keyframes circleShow{
    0%{ -webkit-transform:rotate(0deg); transform:rotate(0deg)}
    100%{ -webkit-transform:rotate(360deg); transform:rotate(360deg)}
}
@-webkit-keyframes circleShow2{
    0%{ -webkit-transform:rotate(360deg); transform:rotate(360deg)}
    100%{ -webkit-transform:rotate(0deg); transform:rotate(0deg)}
}
@keyframes circleShow2{
    0%{ -webkit-transform:rotate(360deg); transform:rotate(360deg)}
    100%{ -webkit-transform:rotate(0deg); transform:rotate(0deg)}
}
.circle >span:nth-child(3){
    -webkit-animation: circleShow 5s linear infinite;
            animation: circleShow 5s linear infinite;
    -webkit-animation-play-state: running;
            animation-play-state: running;
}
.circle >span:nth-child(4){
    -webkit-animation: circleShow2 3s linear infinite;
            animation: circleShow2 3s linear infinite;    
    -webkit-animation-play-state: running;    
            animation-play-state: running;
}
