.menuToggle{
  position:fixed;
  top: 20px; right:20px;
  padding: 1em;
  border-radius: 10px;
  background-color: var(--main-color) ;
  box-shadow: -2px -2px 10px #ddd;
  width: 340px;  height: 430px;
  clip-path: circle(5% at 85% 5%);
  transition: all 0.5s ease-in-out;
  cursor: pointer;
  z-index: 10000;
  animation: animationcolor 5s linear infinite;
}
@keyframes animationcolor{
    0%{filter:hue-rotate(0deg)}
    100%{filter:hue-rotate(360deg)}
}
.menuToggle:hover{
  clip-path: circle(75%);
  background-color: rgba(255,255,255,0.96);
}
.menuBurger{
  position:relative;
  left: 263px; top: 0px;
  width:20px; height: 16px;
  font-weight: bold;
  transition: color 0.5s;
}
.menuBurger span{
  position:absolute;
  left:0; top:0;
  width: 100%; height:2px;
  background: #fff;
}
.menuBurger span:nth-child(1){
  top:0;
}
.menuBurger span:nth-child(2){
  top:5px;
}
.menuBurger span:nth-child(3){
  top:10px;
}
.menuToggle:hover span{
  background: rgba(255,255,255,0)
}
.menuToggle ul {
  position:relative;
  margin: 30px 0 0 20px;
  padding: 0;
  transition: 0.7s ease-in-out;
}
.menuToggle ul li{
  list-style: none;
  margin:0;
  font-size: 4em;
}
.menuToggle ul li a{
  position:relative;
  color: #ddd;
  text-decoration: none;
  font-weight: bold;
}
.menuToggle ul li a::before{
  content:attr(data-text);
  position:absolute;
  top:0;
  width:100%;
  color: #262626;
  overflow: hidden;
  white-space: nowrap;
  transition: 0.5s ease-in-out;
}
.menuToggle ul:hover li a::before{
  cursor: pointer;
  width:0;
}
.menuToggle ul li:hover a::before{
  width: 100%;
}
.cos-cursor{
  z-index: 1000;
  border-radius: 50%;
  width:50px;
  height:50px;
  border:1px solid #000;
  pointer-events: none;
  overflow: hidden;
  transform: translate3d(0,0,0);
  position:fixed;
}